		data(){
			return {
				id: '',
				playState: 0,
				nowPage: 'Discovery'
			}
		},
		methods: {
			playVideo: function () {
				this.playState = 1;
				var videoA = document.getElementById('mainVideo');
				var videoB = document.getElementById('mainVideoMask');
				videoA.play();
				videoB.play();
			},
			pauseVideo: function () {
				this.playState = 2;
				var videoA = document.getElementById('mainVideo');
				var videoB = document.getElementById('mainVideoMask');
				videoA.pause();
				videoB.pause();
			},
			replayVideo: function () {
				this.playState = 1;
				var videoA = document.getElementById('mainVideo');
				var videoB = document.getElementById('mainVideoMask');
				videoA.play();
				videoB.play();
			}
		},
		beforeRouteEnter: function(to, from, next) {
			console.log('beforeRouteEnter');
			next();
		},
		beforeRouteLeave: function(to, from, next) {
			console.log('beforeRouteLeave');
			next();
		},
		beforeDestroy () {
