<template>
	<div class="concernLayer layer">
		<mo-navBar :nowPage="nowPage"></mo-navBar>
		<div class="main">
			<mo-videoItems :videoData="videoData"></mo-videoItems>
		</div>
		<div class="subEmpty" v-if="!videoData.items" ><img src="../../static/image/empty.png"></div>
	</div>
</template>

<script>
	import NavBar from './module/NavBar.vue'
	import VideoItems from './module/VideoItems.vue'

	export default{
		name: 'Concern',
		components: {
			'mo-navBar': NavBar,
			'mo-videoItems': VideoItems
		},
		data(){
			return {
				nowPage: 'Concern',
				videoData: {
					isTitleShow: '',
					items: ''
				}
			}
		},
		methods: {
		},
		beforeRouteEnter: function(to, from, next) {
			console.log('beforeRouteEnter');
			next();
		},
		beforeRouteLeave: function(to, from, next) {
			console.log('beforeRouteLeave');
			next();
		},
		beforeDestroy () {
			console.log('beforeDestroy');
		},
		created: function(){
			console.log('created');
		},
		mounted() {
			console.log('mounted');
		},
		activated() {
			console.log('activated');
		},
		deactivated() {
			console.log('deactivated');
		}
	}
</script>

<style scoped>
	.subEmpty {
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		pointer-events: none;
	}
	.subEmpty img {
		position: absolute;
		bottom: 0;
		right: 15vw;
		height: 80vh;
	}
</style>



